<template>
	<div >
		<div v-html="list"></div>
		
	</div>
</template>

<script>
	export default{
		data(){
			return {
				list:""
			}
		},
		created(){
			this.getData()
		},
		methods:{
			getData(){
				var obj={
					"a":3
				}
				
			this.$Api.Setting(obj).then((res)=>{
				console.log(res)
				this.list=res.q.content
			})
				
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
body{
	font-size:0.34rem ;
}
p{
	text-align: center;
}

</style>


